.cb-home-fast-cash-offer .gform_button {
	background-color: #02bdfc;
	border: 1px solid #0297c9;
	font-size: 1.75rem !important;
	/* font-weight: 700 !important; */
	line-height: 1.5 !important;
	padding: 1.375rem 1.125rem;
	width: 100% !important;
	color: white;
	border-radius: 5px;
}

.cb-home-fast-cash-offer__full-width {
	display: flex;
	justify-content: center;
	padding: 20px 15px 40px;
	width: 100%;
	max-width: none;
}

@media (min-width: 1024px) {
	.cb-home-fast-cash-offer__full-width {
		padding: 20px 0 40px;
	}
}

.cb-home-fast-cash-offer__full-width > * {
	max-width: 1050px;
}

