.cb-home-fast-cash-offer input:focus,
.cb-home-fast-cash-offer textarea:focus,
.cb-home-fast-cash-offer select:focus {
	background-color: rgb(255, 255, 255) !important;
	border-color: rgb(128, 189, 255) !important;
	box-shadow: rgba(0, 123, 255, 0.25) 0px 0px 0px 0.2rem !important;
	color: rgb(73, 80, 87) !important;
	outline: 0px !important;
}

.cb-home-fast-cash-offer .form-control,
.cb-home-fast-cash-offer .wp-block-search .wp-block-search__input,
.cb-home-fast-cash-offer input[type="email"],
.cb-home-fast-cash-offerinput[type="number"],
.cb-home-fast-cash-offer input[type="password"],
.cb-home-fast-cash-offer input[type="search"],
.cb-home-fast-cash-offer input[type="tel"],
.cb-home-fast-cash-offer input[type="text"],
.cb-home-fast-cash-offer input[type="url"],
.cb-home-fast-cash-offer select,
.cb-home-fast-cash-offer select[multiple],
.cb-home-fast-cash-offer textarea {
	background-clip: padding-box;
	background-color: #fff;
	border: 1px solid #ced4da;
	border-radius: 0.25rem;
	color: #495057;
	display: block;
	font-size: 1rem;
	font-weight: 400;
	height: calc(1.5em + 0.75rem + 2px);
	line-height: 1.5;
	padding: 0.375rem 0.75rem;
	transition:
		border-color 0.15s ease-in-out,
		box-shadow 0.15s ease-in-out;
	width: 100%;
}

.cb-home-fast-cash-offer .gform_heading {
	display: none !important;
}